.user-container {
  height: 420px;
  overflow-y: auto;
  position: relative;
}
.user {
  display: flex;
  align-items: center;
  gap: 7px;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 15px;
  }
}

.user__avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.user__message-block {
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.user__author {
  font-weight: bold;
}
